<template>
	<view style='padding-top:2rem'>
		<cu-custom bgColor="bg-gradual-blue" :isBack="true" tourl="/pages/home/index">
			<block slot="backText">返回</block>
			<block slot="content">家庭码扫码</block>
		</cu-custom>
		<view class="top"></view>
		<view class="banner">
			<view class="family">
				<view class="img" @click="sao">
					<image src="https://ljfl.hztlcgj.com/image/scan1.png" mode=""></image>
				</view>
				<view class="address">点击“扫一扫” 扫描家庭码</view>
				<br />
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data: () => {
			return {
				phone: '',
				vuid: '',
				count: "", //倒计时
			};
		},
		methods: {
			sao() {
				var that = this;
				uni.scanCode({
					success: function(res) {
						that.vuid = res.result.split('_')[1];
						console.log('条码类型：' + res.scanType);
						console.log('条码内容：' + that.vuid);
						let data = {
							guid: that.$store.state.userId,
							vuid: that.vuid
						};
						if (that.vuid != null) {
							uni.navigateTo({
								url: '/pages/supervisorInspection/SupervisorInspection'
							});
							wx.setStorageSync('homeuuid', that.vuid)
						} else {
							uni.showModal({
								title: '提示',
								content: "扫描的码不是家庭码！",
								showCancel: false
							})
						}


					}
				})
			}
		}
	}
